<template>
  <div>
    <el-row style="background-color:#F2F2F2;margin-top:10px;margin-bottom:5px;">
        <el-col :span="3">
            <el-button type="primary" @click='handleAddTerm' icon="plus">新增</el-button> 
        </el-col>
    </el-row>
    <el-table
        v-loading="loading"
        element-loading-text="加载数据"
        :data="termDatas"
        style="width:100%"  
        empty-text="暂无数据"
        highlight-current-row
        >              
        <el-table-column label="ID" property="id"  min-width="50"  header-align="center">
        </el-table-column>
        <el-table-column label="术语ID" property="conceptId"  min-width="130"  header-align="center">
        </el-table-column>
        <el-table-column label="术语父ID"  property="pconceptId" min-width="130"  header-align="center">
        </el-table-column>
        <el-table-column label="术语编码" property="conceptCode" min-width="100" header-align="center">
        </el-table-column>
        <el-table-column label="原始名称" property="originName" min-width="160" header-align="center" >  
        </el-table-column> 
        <el-table-column label="术语名称" property="conceptName" min-width="160" header-align="center" >   
        </el-table-column>
        <el-table-column label="术语集合" property="conceptType" min-width="160" header-align="center" >
        </el-table-column>
        <el-table-column label="操作"  min-width="180"
          header-align="center">
          <template  slot-scope="scope">
            <el-button type="primary" size="small" icon="circle-check"
                       @click='handleUpdateTerm(scope.row)'>更新</el-button>
            <el-button type="success" size="small" icon="time"
                       @click='handleDeleteTerm(scope.row.id)'>删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                :current-page.sync="pageIndex" :page-sizes="[10,20,30,50]" :page-size="pageSize" layout="total, sizes, prev, pager, next, jumper" :total="totalLimit">
      </el-pagination>
      <el-dialog title="编辑术语" :visible.sync='editTermVisible' size="tiny">
        <el-form :model="currentTerm" label-position="left" label-width="70px">
          <el-form-item label="术语ID">
            <el-input v-model="currentTerm.conceptId" :disabled="true"></el-input>
          </el-form-item> 
          <el-form-item label="术语名称">
            <el-input v-model="currentTerm.conceptName"></el-input>
          </el-form-item>     
          <el-form-item label="是否新增">
            <el-switch
              v-model="currentTerm.rank"
              on-color="#13ce66"
              off-color="#ff4949"
              on-text="新增"
              off-text="更新"
              on-value="add"
              off-value="update">
          </el-switch>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" @click="cUpdateTerm">确认</el-button>
            <el-button @click='editTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
      <el-dialog title="新增术语" :visible.sync='addTermVisible' size="tiny">
        <el-form :model="currentTerm" label-position="left" label-width="70px">
          <el-form-item label="术语ID">
            <el-input v-model="currentTerm.conceptId"></el-input>
          </el-form-item>
          <el-form-item label="父术语ID">
            <el-input v-model="currentTerm.pconceptId"></el-input>
          </el-form-item>
          <el-form-item label="术语名称">
            <el-input v-model="currentTerm.conceptName"></el-input>
          </el-form-item>     
          <el-form-item label="术语编码">
            <el-input v-model="currentTerm.conceptCode"></el-input>
          </el-form-item>    
          <el-form-item label="术语集合">
            <el-input v-model="currentTerm.conceptType"></el-input>
          </el-form-item>    
          <el-form-item>
            <el-button type="primary" @click="cAddTerm">确认</el-button>
            <el-button @click='addTermVisible=false'>关闭</el-button>
         </el-form-item>
        </el-form>
      </el-dialog>
  </div>
</template>
<script>
    import {getTerms,updateTerm,addTerm,deleteTerm} from '@/api/term'
    export default{
      data () {
        return {
          termDatas:null,
          loading:false,
          pageIndex:0,
          totalPages:0,
          totalLimit:0,
          pageSize:10,
          editTermVisible:false,
          addTermVisible:false,
          currentTerm:{
              id:null,
              conceptId:null,
              pconceptId:null,
              conceptCode:null,
              conceptType:null,
              conceptName:null,
              originName:null,
              rank:'update'
            }
          }
      },
      created(){
        var self=this
        self.initTable()
      },
      methods: {
        handleSizeChange(val){
          var self=this;
          self.pageSize=val;
          self.initTable();
        },
        handleCurrentChange(val){
          var self=this;
          self.pageIndex=val;
          self.initTable();
        },
        initTable(){
          var self=this
          self.loading=true
          getTerms(self.pageIndex,self.pageSize)
          .then((response)=>{
            console.log(response)
            self.totalPages=response.data.pages;
            self.totalLimit=response.data.total;
            self.termDatas=response.data.dataList
            self.loading=false
          })
        },
        cUpdateTerm(){
          var self=this;
          if(self.currentTerm.rank=='add'){
            console.log(self.currentTerm)
              addTerm(self.currentTerm.conceptId,self.currentTerm.pconceptId,self.currentTerm.conceptName,
                self.currentTerm.conceptCode,self.currentTerm.conceptType,self.currentTerm.originName)
              .then((response)=>{
                  if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.editTermVisible=false
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
              })
          }
          if(self.currentTerm.rank=='update'){
              updateTerm(self.currentTerm.id,self.currentTerm.conceptId,self.currentTerm.conceptName)
              .then((response)=>{
              if(response.succ==true){
                  self.$message({
                  type:'success',
                  message:'更新成功！'
                  });
                  self.editTermVisible=false
                  self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          }
        },
        cAddTerm(){
          var self=this;
          addTerm(self.currentTerm.conceptId,self.currentTerm.pconceptId,self.currentTerm.conceptName,
                self.currentTerm.conceptCode,self.currentTerm.conceptType,self.currentTerm.conceptName)
              .then((response)=>{
                  if(response.succ==true){
                  self.$message({
                    type:'success',
                    message:'添加成功！'
                  });
                  self.addTermVisible=false
                  self.initTable()
                }else{
                  self.$message.error(response.msg)
                }
              })
        },
        handleAddTerm(){
          var self=this
          self.addTermVisible=true
          self.currentTerm.id=null
          self.currentTerm.conceptId=null
          self.currentTerm.pconceptId=null
          self.currentTerm.conceptName=null
          self.currentTerm.conceptCode=null
          self.currentTerm.conceptType=null
          self.currentTerm.originName=null
        },
        handleUpdateTerm(currentRow){
          var self=this;
          self.editTermVisible=true;
          self.currentTerm.id=currentRow.id;
          self.currentTerm.conceptId=currentRow.conceptId;
          self.currentTerm.pconceptId=currentRow.pconceptId;
          self.currentTerm.conceptName=currentRow.conceptName;
          self.currentTerm.conceptCode=currentRow.conceptCode;
          self.currentTerm.conceptType=currentRow.conceptType;
          self.currentTerm.originName=currentRow.originName;
        },
        handleDeleteTerm(pId){
            var self=this;
            self.$confirm('确认删除该类型？','提示',{
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(()=>{
             deleteTerm(pId)
            .then((response)=>{
              if(response.succ==true){
                self.$message({
                  type:'success',
                  message:'删除成功！'
                });
                self.initTable()
              }else{
                self.$message.error(response.msg)
              }
            })
          })
        }
      }
    }
</script>



